import React from "react";
import jumbo from "../assets/jumbo-contact.jpg";
import "../framework/site.scss";

export default class ContactBannerSection extends React.PureComponent {
  render() {
      const rootStyle = {
          backgroundImage: 'url(' + jumbo + ')'
      };

      return (
          <section className="intro" style={rootStyle}>
              <h1>
                  Contact us
              </h1>
          </section>
    );
  }
}